import React from 'react';
import { motion } from 'framer-motion';

const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: { y: 0, opacity: 1, transition: { type: 'spring' } },
};

const CityInfo = ({ name, info }) => {
  if (!info || !info.imageUrl) {
    return (
      <motion.div className="city-info-card placeholder" variants={itemVariants}>
          <h3>{name}</h3>
          <p>No se encontr informacin adicional para esta ciudad.</p>
      </motion.div>
    );
  }

  return (
    <motion.div className="city-info-card" variants={itemVariants}>
      <img src={info.imageUrl} alt={`Fotografa de ${name}`} className="city-image" />
      <div className="city-text">
        <h3>{name}</h3>
        <p>{info.description || "No se encontr una descripcin."}</p>
      </div>
    </motion.div>
  );
};

export default CityInfo;